"use client";

import { ConnectButton } from '@rainbow-me/rainbowkit';
import { useAccount } from 'wagmi';
import { useEffect, useState } from 'react';

export function Header() {
  const { isConnected } = useAccount();
  const [mounted, setMounted] = useState(false);

  // Only render connection status after client-side hydration
  useEffect(() => {
    setMounted(true);
  }, []);

  return (
    <header className="w-full px-3 md:px-0 h-fit py-10">
      <nav className="flex w-full justify-between items-center">
        <div className="flex items-center gap-4">
          <div className="flex items-center justify-center w-12 h-12 bg-black rounded-lg">
            <span className="text-white text-xl">üîê</span>
          </div>
          <div>
            <h1 className="text-2xl font-bold text-black">Inheritance Rule Check</h1>
            <p className="text-sm text-gray-600">Verify eligibility privately</p>
          </div>
        </div>
        <div className="flex items-center gap-3">
          {mounted && isConnected && (
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span>Connected</span>
            </div>
          )}
          <ConnectButton />
        </div>
      </nav>
    </header>
  );
}

